* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  font-family: "Work Sans", sans-serif;
}

body {
  font-family: "Work Sans", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
